import React, { useEffect, useContext } from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import Img from "gatsby-image";
import { MDXRenderer } from "gatsby-plugin-mdx";
import { motion, useAnimation } from "framer-motion";

import Context from "../../context/";
import ContentWrapper from "../../styles/contentWrapper";
import Underlining from "../../styles/underlining";
import Social from "../social";
import { lightTheme, darkTheme } from "../../styles/theme";

const StyledSection = styled.section`
  width: 100%;
  height: auto;
  background: ${({ theme }) => theme.colors.background};
`;

const StyledContentWrapper = styled(ContentWrapper)`
  && {
    width: 100%;
    height: 100%;
    min-height: 30vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 6rem;
    @media (min-width: ${({ theme }) => theme.breakpoints.sm}) {
      margin-bottom: 4rem;
    }
    .greetings {
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }
    .emoji {
      margin-left: 0.75rem;
      width: 2.2rem;
      height: 2.2rem;
      @media (min-width: ${({ theme }) => theme.breakpoints.sm}) {
        margin-left: 1rem;
        width: 3rem;
        height: 3rem;
      }
    }
    .title {
      margin-bottom: 1.5rem;
      @media (min-width: ${({ theme }) => theme.breakpoints.sm}) {
        margin-bottom: 0.5rem;
      }
      font-size: 50px;
      line-height: normal;
    }
    .subtitle {
      margin-top: -0.75rem;
      color: #e74c3c;
    }
    .description {
      font-size: 1.125rem;
      margin-bottom: 2rem;
    }
  }
`;

const AnimatedUnderlining = motion.custom(Underlining);

const Hero = ({ content, showSocial }) => {
  const { frontmatter, body } = content[0].node;
  const { isIntroDone, darkMode } = useContext(Context).state;

  // Controls to orchestrate animations of greetings, emoji, social profiles, underlining
  const gControls = useAnimation();
  const eControls = useAnimation();
  const sControls = useAnimation();
  const uControls = useAnimation();

  // Start Animations after the splashScreen sequence is done
  useEffect(() => {
    const pageLoadSequence = async () => {
      if (isIntroDone) {
        eControls.start({
          rotate: [0, -10, 12, -10, 9, 0, 0, 0, 0, 0, 0],
          transition: { duration: 2.5, loop: 3, repeatDelay: 1 },
        });
        await gControls.start({
          opacity: 1,
          y: 0,
          transition: { delay: 0.4 },
        });
        await sControls.start({
          opacity: 1,
          x: 0,
        });
        // Animate underlining to hover state
        await uControls.start({
          boxShadow: `inset 0 -2rem 0 ${
            darkMode ? darkTheme.colors.secondary : lightTheme.colors.secondary
          }`,
          transition: { delay: 0.4, ease: "circOut" },
        });
      }
    };
    pageLoadSequence();
  }, [isIntroDone, darkMode, eControls, gControls, sControls, uControls]);
  console.log(frontmatter.title);
  if (frontmatter.title === "I'm Edward Jung") {
    return (
      <StyledSection id="hero">
        <StyledContentWrapper>
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={gControls}
            data-testid="animated-heading"
          >
            <h1 className="title">
              <div className="greetings">
                {frontmatter.greetings}
                <motion.div
                  animate={eControls}
                  style={{ originX: 0.7, originY: 0.7 }}
                >
                  <Img
                    className="emoji"
                    fluid={frontmatter.greetingicon.childImageSharp.fluid}
                  />
                </motion.div>
              </div>
              {frontmatter.title}
            </h1>
            <h2
              className="subtitle"
              style={{
                color: frontmatter.color ? frontmatter.color : "#69b4ff",
              }}
            >
              {frontmatter.subtitlePrefix}{" "}
              <AnimatedUnderlining animate={uControls} big>
                {frontmatter.subtitle}
              </AnimatedUnderlining>
            </h2>
            <div className="description">
              {body && <MDXRenderer>{body}</MDXRenderer>}
            </div>
          </motion.div>
          {showSocial && (
            <motion.div initial={{ opacity: 0, x: 20 }} animate={sControls}>
              <Social
                fontSize=".95rem"
                padding=".3rem 1.25rem"
                width="auto"
                withIcon={true}
              />
            </motion.div>
          )}
        </StyledContentWrapper>
      </StyledSection>
    );
  } else {
    return (
      <StyledSection id="hero">
        <StyledContentWrapper>
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={gControls}
            data-testid="animated-heading"
          >
            <h1 className="title">
              <div className="greetings">{frontmatter.greetings}</div>
              {frontmatter.title}
            </h1>
            <h2
              className="subtitle"
              style={{
                color: frontmatter.color ? frontmatter.color : "#69b4ff",
              }}
            >
              {frontmatter.subtitlePrefix}{" "}
              <AnimatedUnderlining animate={uControls} big>
                {frontmatter.subtitle}
              </AnimatedUnderlining>
            </h2>
            <div className="description">
              {body && <MDXRenderer>{body}</MDXRenderer>}
            </div>
          </motion.div>
          {showSocial && (
            <motion.div initial={{ opacity: 0, x: 20 }} animate={sControls}>
              <Social
                fontSize=".95rem"
                padding=".3rem 1.25rem"
                width="auto"
                withIcon={true}
              />
            </motion.div>
          )}
        </StyledContentWrapper>
      </StyledSection>
    );
  }
};

Hero.propTypes = {
  content: PropTypes.arrayOf(
    PropTypes.shape({
      node: PropTypes.shape({
        body: PropTypes.string.isRequired,
        frontmatter: PropTypes.object.isRequired,
      }).isRequired,
    }).isRequired
  ).isRequired,
  showSocial: PropTypes.bool.isRequired,
};

export default Hero;
